@import '../../../template.scss';

.registration-stages {
  @include main-container();
  background-color: #fff;
}


.registration-stages-block__stage {
  border: 1px solid #c1c1c1;
  border-radius: size(18px);
  padding: size(58px) size(118px);
  margin-bottom: size(20px);
  display: flex;
  align-items: center;
}
.registration-stage__number {
  font-size: size(200px);
  color: #1260F5;
  font-weight: bold;
}

.stage__content {

}

.registration-block-stage__title {
  margin-bottom: size(48px);
  font-size: size(23px);
  font-weight: 500;

}

.registration-block-stage__text {
  color: rgba(0,0,0, 0.4);
  font-size: size(15px);
}

.registration-block-stage__link {

}
